.contact-form{  
    display: inline-block;
    text-align: center;
    width: 460px; 
    margin: 120px auto;
    border-radius: 10px;  
    padding-top: 30px;
    padding-bottom: 20px;  
    background-color: #fbfbfb; 
    padding-left: 30px; 
}

.contact-form input{
    background-color: #fbfbfb; 
    width: 408px; 
    height: 40px; 
    border-radius: 5px;  
    border-style: solid; 
    border-width: 1px; 
    border-color: #34D2AF; 
    margin-top: 10px;  
    padding-left: 10px;
    margin-bottom: 20px; 
}

.contact-form textarea{
    background-color: #fbfbfb; 
    width: 405px; 
    height: 150px; 
    border-radius: 5px;  
    border-style: solid; 
    border-width: 1px; 
    border-color: #34D2AF; 
    margin-top: 10px;  
    padding-left: 10px;
    margin-bottom: 20px; 
    padding-top: 15px; 
}

.contact-form label{
    display: block; 
    float: center;  
}

.contact-form .button-enviar {
    align-items: center;
    height: 25px; 
    padding-left: 5px;
    padding-right: 5px;   
    margin-bottom: 20px; 
    margin-top: 10px;   
    text-transform: uppercase;
    background-color: #34D2AF; 
    border-color: #007259; 
    border-style: solid; 
    border-radius: 10px;  
    width: 420px;   
    cursor: pointer;
}

.contact-form button p{
    color: #fff; 
}

.contact-form span{
    color: #ab4493; 
}

.aviso{
    font-size: 13px;  
    color: #0e0e0e;  
}

.contact-form h1{
    font-size: 39px;  
    text-align: letf; 
    padding-bottom: 20px; 
    color: #ab4493;
}

.contact-form h3{
    font-size: 16px; 
    padding-bottom: 30px;
    color: #0e0e0e;   
}

.contact-form p{
    font-size: 14px; 
    color: #0e0e0e; 
}

::-webkit-input-placeholder {
    color: #a8a8a8;
}

::-webkit-textarea-placeholder {
    color: #a8a8a8;
}

.contact-form input:focus{
    outline:0;
    border: 1px solid #97d848;
}

.contact-form textarea:focus{
    outline:0;
    border: 1px solid #97d848;
}
